.profile__title {
    margin: 0 0 0 26px;
    padding: 0;
    font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 27px;
    line-height: 33px;
    color: #FFFFFF;
    text-overflow: ellipsis;
    overflow: hidden;
}

@media screen and (min-width: 880px) {
    .profile__title {
        margin: 0;
        font-size: 42px;
        line-height: 48px;
    }
}